import {Button, Layout, Menu, Space} from "antd";
import {CloseOutlined, FacebookOutlined, InstagramOutlined, LinkedinOutlined, MenuOutlined} from "@ant-design/icons";
import React, {useState} from "react";
import './SideMenu.css'

export const SideMenu = () => {
  const [collapsed, setCollapsed] = useState(true)

  return <Layout.Sider trigger={null} collapsible collapsed={collapsed} collapsedWidth={"60px"} width={"300px"}>
    <Menu theme="dark" mode="inline" className={"sideMenu"}>
      <Button icon={collapsed ? <MenuOutlined/> : <CloseOutlined/>} onClick={() => {
        setCollapsed(!collapsed)
      }}/>
      {!collapsed
      && <Menu.ItemGroup>
        <Menu.Item key="1" className={"menuItem"}>
          Парковка
        </Menu.Item>
        <Menu.Item key="2" className={"menuItem"}>
          Страховка
        </Menu.Item>
        <Menu.Item key="3" className={"menuItem"}>
          Бензин
        </Menu.Item>
        <Menu.Item key="4" className={"menuItem"}>
          Обслуживание
        </Menu.Item>
        <Space style={{marginTop: '30px'}}>
          <FacebookOutlined className={"menuItem"}/>
          <LinkedinOutlined className={"menuItem"}/>
          <InstagramOutlined className={"menuItem"}/>
        </Space>
      </Menu.ItemGroup>
      }
      <Button shape={"circle"} className={"langChanger"}>Eng</Button>
    </Menu>
  </Layout.Sider>
}